
.content{
      width: 974px;
      height: 690px;
      position: relative;
      display: flex;
      align-items: center;
}

.content:before{
      width: 100%;
      height: 590px;
      background: #000;
      left: 0;
      top: calc(50% - 295px);
      z-index:  -1;
      filter: blur(50px);
      opacity: .4;
}