.friends_group__inner{
      width: 290px;
      margin: 0 auto;
}

.card_friend{
      width: 90px;
      height: 90px;
}

.card_friend a{
      display:  flex;
      position: relative;
      width: 90px;
      height: 90px;
      border: solid 3px var(--purple-100);
      overflow: hidden;
      border-radius: .4rem;
      transition: all .2s ease-in-out;
}

.card_friend:hover a{
      border:  solid 3px var(--purple-300);
}

.card_friend a img{
      width: 84px;
      height: 84px;
      overflow: hidden;
      border-radius: .2rem;
}

.card_friend a span{
      position: absolute;
      background: var(--purple-300);
      color: var(--light);
      text-transform: uppercase;
      padding: .3rem .6rem;
      border-radius: 2rem;
      left: 50%;
      transform: translateX(-50%);
      bottom: -3rem;
      transition: .3s ease-in-out;
}

.card_friend:hover a span{
      bottom: 2rem;
}

.slick-slide{
      margin: 0 .5rem;
}

.slick-list{
      height: 120px;
}

.slick-slider{
      position: relative;
}

.slick-arrow{
      width: 36px;
      height: 36px;
      position: absolute;
      top: calc(50% - 18px);
      border-radius: 50%;
}

.slick-arrow:before{
      width: 10px;
      height: 10px;
      left: 50%;
      top: 50%;
      color: transparent;
      border-top: solid 2px var(--purple-300);
      border-left: solid 2px var(--purple-300);
}

.slick-arrow.slick-prev:before{
      transform: translate(-50%, -50%) rotate(-45deg);
}

.slick-arrow.slick-next:before{
      transform: translate(-50%, -50%) rotate(135deg);
}


.slick-arrow.slick-prev{
      left: -3.6rem;
}

.slick-arrow.slick-next{
      right: -3.6rem;
}