.wrapper_container{
      flex-grow: 1;
      display: flex;
      background: var(--bg-grad);
      height: 690px;
      width: 680px;
}

/** data details */
.wrapper_data{
      width: 295px;
      height: 590px;
      background: var(--light);
      flex-shrink: 0;
      margin-top: 5rem;
      padding: 2rem 1.5rem;
}

.wrapper_online{
      width: 100%;
      height: 32px;
      display: flex;
      gap: 1rem;
}

.online_avatar,
.online_avatar__img{
      width: 32px;
      height: 32px;
      position: relative;
}

.online_avatar__img{
      border-radius: 50%;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
}

.dots{
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: var(--light);
      box-shadow: 0 0 0 2px var(--light);
      position: absolute;
      top: 0;
      right: 0;
}

.dots.online{
      background: var(--green);
}

.dots.offline{
      background: var(--red);
}

.online_details{
      width: 90px;
      height: 32px;
      border-radius: 16px;
      background: var(--purple-300);
      text-transform: uppercase;
      color: var(--light);
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: 600;
}

/** galleries */
.wrapper_gallery{
      margin-top: 1.5rem;
      width: 100%;
}

.wrapper_gallery h2,
.wrapper_social h2,
.wrapper_friends h2{
      width: 100%;
      text-transform: uppercase;
      font-size: 1.2rem;
}

.gallery_content{
      padding: 1.2rem 0;
      border-top: solid 1px var(--border);
      border-bottom: solid 1px var(--border);
      margin-top: .5rem;
}

.wrapper_social{
      margin-top: 1rem;
}

.social_content{
      margin-top: .5rem;
}

.wrapper_user{
      width: 384px;
      flex-grow: 1;
      height: 640px;
      margin-top: 5rem;
      display: flex;
      flex-direction: column;
      align-items: center;
}

.user_content{
      width: 354px;
      height: 315px;
      background: var(--border);
      padding: 1rem;
      display: flex;
      gap: 1rem;
}

.user_avatar{
      width: 80px;
      height: 80px;
      background: var(--purple-100);
      border-radius: 50%;
      flex-shrink: 0;
      margin: .5rem;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      box-shadow: 0 0 0 .5rem #d3d0d7;
}

.user_avatar img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.user_details{
      flex-grow: 1;
}

.user_details__header{
      position: relative;
      width: 100%;
      height: auto;
}

.user_details__header h1{
      font-size: 2rem;
      margin-top: .5rem;
}

.user_details__header h1 sup{
      padding: .5rem 1rem;
      border-radius: 999px;
      background: #d3d0d7;
}

.user_details__header span{
      color: var(--text);
      font-size: 1.2rem;
}

.user_location{
      margin-top: 1rem;
      display: flex;
      gap: .5rem;
      align-items: flex-end;
}

.user_location *{
      color: var(--purple-300);
      font-size: 2.5rem;
}

.user_location span{
      font-size: 1.2rem;
}

.user_details__about{
      width: 100%;
      height: 200px;
      margin-top: 1rem;
      overflow: hidden;
}

.user_details__about p{
      color: var(--text);
      font-size: 1.4rem;
}

/** relation */
.wrapper_relation{
      width: 100%;
      height: 140px;
      position: relative;
      background: var(--purple-100);
      display: flex;
      gap: 1rem;
      padding: 2rem 2.5rem;
}

.relation_type{
      height: 32px;
      padding: 0 1.5rem;
      position: absolute;
      left: 2.5rem;
      top: -16px;
      border-radius: 32px;
      background: var(--purple-400);
      color: var(--light);
      text-transform: uppercase;
      line-height: 32px;
      font-size: 1.2rem;
      font-weight: 500;
}

.relation_data{
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
}

.relation_data h1{
      font-size: 2rem;
}

.relation_data span{
      color: var(--text);
      font-size: 1.4rem;
      font-weight: 700;
}

.relation_about{
      width: 244px;
      height: 80px;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--purple-200) var(--purple-400);
}

.relation_about::-webkit-scrollbar{
      width: .8rem;
      background: var(--purple-200);
      border-radius: .8rem;
}

.relation_about::-webkit-scrollbar-thumb{
      background: var(--purple-400);
      border-radius: .8rem;
}

.relation_about p{
      font-size: 1.2rem;
      color: var(--text);
}

.relation_avatar{
      width: 80px;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 1rem;
      flex-shrink: 0;
}

.relation_avatar_img{
      width: 70px;
      height: 70px;
      background: var(--border);
      border-radius: 50%;
      overflow: hidden;
      margin: .5rem;
      margin-bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      box-shadow: 0 0 0 .5rem #d6d2d8;
      flex-shrink: 0;
}

.relation_avatar_img img{
      width: 70px;
      height: 70px;
      border-radius: 50%;
}

.relation_avatar a{
      padding: .5rem 1rem;
      text-transform: uppercase;
      background: var(--purple-300);
      border-radius: 999px;
      color: var(--light);
}

.wrapper_friends{
      width: 100%;
      margin-top: 1rem;
      padding: 0 1.5rem;
}

.wrapper_friends h2{
      line-height: 2rem;
      display: flex;
      justify-content: space-between;
}

.wrapper_friends h2 span{
      height: 2rem;
      padding: 0 1rem;
      background: var(--purple-300);
      color: var(--light);
      border-radius: 2rem;
}

.friends_content{
      width: 100%;
      border-top: solid 1px #d8d8d8;
      margin-top: .5rem;
      padding-top: 1rem;
}

.friends_group{
      width: 100%;
}