@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&family=Roboto:wght@100;300;400;500;700;900&display=swap');

:root{
  /* fonts */
  --ft-1: 'Lato',sans-serif;
  --ft-2: 'Roboto', sans-serif;

  /* colors */
  --gradient:  linear-gradient(135deg, #635370, #2f1b40);
  --bg-grad: linear-gradient(#5e3776 365px, #f0f0f0 365px);
  --purple-100: #e9e5eb;
  --purple-200: #8f68a9;
  --purple-300: #846398;
  --purple-400: #5e3776;
  --black: #111;
  --light: #fff;
  --text: #a2a2a2;
  --border:  #f0f0f0;
  --bg-btn-s: #edebef;
  --green: #65b87f;
  --red: #d90000;

  /* globals */
  font-size: 62.5%;
  font-family: var(--ft-2);
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  min-height: 880px;
  overflow-x: hidden;
  background: var(--gradient);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
}

.container{
  width: 1064px;
  height: auto;
  position: relative;
  margin: 0  auto;
}