.banner{
      width: 295px;
      height: 590px;
      position: relative;
      box-shadow: 0 -5rem var(--light) inset;
      background: var(--purple-300);
      flex-shrink: 0;
}

.banner:before{
      width: 60px;
      height: 60px;
      background: #6f5380;
      border-radius: 50%;
      top: 2rem;
      left: 2rem;
      box-shadow: 0 0 0 .6rem var(--purple-300);
      z-index: 2;
}

.banner_img{
      width: 255px;
      height: 550px;
      position: absolute;
      bottom: 0;
      right: 0;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
}

.banner_img img{
      width:auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}