.empty_data{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 385px;
      gap: 1rem;
}

.empty_data *{
      color: var(--text);
      font-size: 2rem;
}

.empty_data span *{
      font-size: 5rem;
}

.gallery_group{
      width: 100%;
      height: 385px;
      overflow-y: auto;
      scrollbar-color: var(--purple-100) var(--purple-400);
      scrollbar-width: thin;
}

.gallery_group::-webkit-scrollbar{
      width: .8rem;
      background: var(--purple-100);
      border-radius: .8rem;
}

.gallery_group::-webkit-scrollbar-thumb{
      background: var(--purple-400);
      border-radius: .8rem;
}

.gallery_list{
      width: 255px;
      height: auto;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: .5rem;
}

.gallery_item{
      height: 125px;
      position: relative;
      overflow: hidden;
      border-radius: .4rem;
      display: flex;
      align-items: center;
      justify-content: center;
}

.gallery_item img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      opacity: .8;
      transition: .2s;
}

.gallery_item:hover img{
      opacity: 1;
}

.btn_gallery{
      position: absolute;
      width: 36px;
      height: 36px;
      border-radius: 50%;
      background: rgba(0,0,0,.5);
      transform: scale(1.4);
      opacity: 0;
      transition: .2s ease-in-out;
}

.btn_gallery *{
      color: var(--light);
      font-size: 1.4rem;
}

.gallery_item:hover .btn_gallery{
      transform: scale(1);
      opacity: 1;
}

.wrapper_modal{
      position: fixed;
      width: 100vw;
      height: 100vh;
      background: rgba(0,0,0,.6);
      top: 0;
      left: 0;
      z-index: 1000;
      opacity: 0;
      visibility: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
}

.wrapper_modal.show{
      animation: show_modal .3s ease-in-out forwards;
}

@keyframes show_modal{
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.btn_close{
      width: 40px;
      height: 40px;
      border-radius: 50%;
      position: absolute;
      top: 2rem;
      right: 2rem;
      background: rgba(0,0,0,.3);
      transform: rotate(45deg);
}

.btn_close *{
      color: var(--light);
      font-size: 2rem;
}

.modal_inner{
      width: 80%;
      height: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0;
      visibility: hidden;
      transform: scale(1.4);
}

.modal_inner.show{
      animation: show_img .2s .3s ease-in-out forwards;
}

@keyframes show_img{
      100%{
            opacity: 1;
            visibility: visible;
            transform: scale(1);
      }
}

.modal_inner img{
      width: initial;
      max-width: 100%;
      height: auto;
      max-height: 80vh;
}