
.empty_data.social{
      height: 34px;
      margin-top: 1rem;
}

.empty_data.social h3{
      font-size: 1.2rem;
}

.social_group{
      height: 34px;
      margin-top: 1rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
}

.social_group a{
      width: 34px;
      height: 34px;
      background: rgba(237, 235, 239,.4);
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: .4rem;
      transition: .2s;
}

.social_group a:hover{
      background: rgba(237,235,239,1);
}

.social_group a *{
      font-size: 1.6rem;
      color: var(--purple-400);
}